.results__div {
  margin: 3%;
}
.results__img {
  width: 280px;
  position: relative;
  transition: all 0.2s ease-in-out;
  overflow: hidden;
  height: 340px;
  transition: all .4s linear;
}

.result__items {
  display: grid;
  margin: 0 auto;
  justify-content: center;
  grid-template-rows: 1fr 1fr 1fr 1fr;
  grid-template-columns: repeat(auto-fill, minmax(300px, auto));
  margin-top: 5%;
}
.result__align {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  position: relative;
  overflow: hidden;
  font-family: Arial, Helvetica, sans-serif;
}
.result__align:hover .results__img {
  opacity: 0.2;
  transform: scale(1.1);
}
.result__align:hover .result__info {
  color: white;
  display: block;

  
}
.result__info {
  display: none;
  text-shadow: 2px 5px 30px rgba(231, 231, 231, 0.26);
  text-align: center !important;
  font-size: 19px;
  position: absolute;
  transition: all .2s linear;
  line-height: 30px;
  
}
.result__info >h4, .result__info > p{
    font-weight: 200;
}
