.Navbar__div {
  width: 100vw;
  height: auto;
  display: flex;
  justify-content: space-between;
  padding: 20px;
  align-items: center;
  margin: 0 auto;
}
.Navbar__div img:first-child {
  width: 80px !important;
}
.Navbar__div img {
  object-fit: contain;
  width: 40px;
  height: 30px;
}
@media only screen and (max-width: 600px) {
    .Navbar__div a:last-child {
       right: -20px;
       position: relative;
    }
}