.search__div{
    width: 100vw;
    height: auto;
    display: flex;
    justify-content: center;
    
}
.search__div form{
    width: 100%;
    display: flex;
    justify-content: center;
    padding: 0px 20px;
    height: 35px;
}
.search__div form input {
    z-index: 2;
    width: 80%;
    border-radius: 15px;
    outline: none;
    border-color: transparent;
    padding-left: 16px;
    background-color: #24232359;
    color: white;
    font-size: 17px;
    box-shadow: -10px 10px 25px rgba(255, 255, 255, 0.055);
    transition: all .3s ease-in-out;
    border-bottom: 0px solid rgba(255, 255, 255, 0);
    
}
.search__div form input:focus{
    letter-spacing: 1px;
    border-bottom: 2px solid white;
}

.search__div form button{
    cursor: pointer;
    margin-left: 10px;
    outline: none;
    border-color: transparent;
    background-color: #242323c4;
    color: rgba(255, 255, 255, 0.568);
    text-align: center!important;
    font-size: 17px;
    box-shadow: -10px 10px 20px rgba(73, 73, 73, 0.151);
    width: 90px;
    border-radius: 10px;
    
}
.search__div form button:hover{
    color: rgba(255, 255, 255, 0.842);
}
@media only screen and (max-width: 600px) {
    .search__div form input{
        font-size: 13px;
        border-radius: 10px
    }
    .search__div form input:focus{
        letter-spacing: 0px;
        
    }
    .search__div form button{
        font-size: 13px;
        width: 60px;
        border-radius: 5px;
    }
}